import React from 'react';

function Pagination({ counter, setCounter }) {
  const handleClick = (e) => {
    setCounter(parseInt(e.currentTarget.textContent));
  };
  return (
    <>
      <div className="container-sm">
        <nav aria-label="Page navigation example">
          <ul className="pagination justify-content-center mb-5">
            <li className="page-item">
              <button
                className="btn btn-outline-danger"
                onClick={() => {
                  if (counter > 1) {
                    setCounter(counter - 1);
                  } else {
                    setCounter(1);
                  }
                }}
              >
                <a href="#my-cards">&laquo;</a>
              </button>
            </li>
            {counter >= 2 ? (
              <>
                <li className="page-item">
                  <button
                    className="btn btn-outline-danger"
                    onClick={handleClick}
                  >
                    <a href="#my-cards">{counter - 1}</a>
                  </button>
                </li>
                <li className="page-item">
                  <button className="btn btn-danger" onClick={handleClick}>
                    <a href="#my-cards">{counter}</a>
                  </button>
                </li>
                <li className="page-item">
                  <button
                    className="btn btn-outline-danger"
                    onClick={handleClick}
                  >
                    <a href="#my-cards">{counter + 1}</a>
                  </button>
                </li>{' '}
              </>
            ) : (
              <>
                {' '}
                <li className="page-item">
                  <button
                    className="btn btn-outline-danger"
                    onClick={handleClick}
                  >
                    {counter}
                  </button>
                </li>
                <li className="page-item">
                  <button
                    className="btn btn-outline-danger"
                    onClick={handleClick}
                  >
                    <a href="#my-cards">{counter + 1}</a>
                  </button>
                </li>{' '}
              </>
            )}
            <li className="page-item">
              <button
                className="btn btn-outline-danger"
                onClick={() => {
                  setCounter(counter + 1);
                }}
              >
                <a href="#my-cards">&raquo;</a>
              </button>
            </li>
          </ul>
        </nav>
      </div>
    </>
  );
}

export default Pagination;
